import React from "react";
import { HiOutlineDotsVertical } from "react-icons/hi";
import { useSelector } from "react-redux";
import styled from "styled-components";
import { theme } from "../../data/ThemeColor";
import { useMarksEntry } from "../../Context/MarksEntry";
import { useAddStudent } from "../../Context/AddStudent";
import useAxios from "../../api/Axios";
import { handleError, handleSuccess } from "../../utils/Toastify";
import { useLocation, useNavigate } from "react-router-dom";

const ViewInfo = React.lazy(() => import("./IndividualAction/ViewInfo"));
const ImportStudentInfo = React.lazy(() =>
  import("./AddStudent/ImportStudent")
);
const AddMarks = React.lazy(() => import("./MarksEntry/AddMarks"));

const TopInformationPanel = ({ classInfo, onReloadPage }) => {
  const axios = useAxios();
  const marks = useMarksEntry();
  const students = useAddStudent();
  const navigate = useNavigate();
  const location = useLocation();
  const [openOption, setOpenOption] = React.useState(false);
  const [loading, setLoading] = React.useState(true);

  const { id, flag } = useSelector((state) => state.viewInfo);

  React.useEffect(() => {
    marks.handleSetClassInfo(classInfo);
    students.handleSetClassInfo(classInfo);
    setLoading(false);
    if (students.reload || marks.reload) {
      onReloadPage();
    }
  }, [classInfo, marks, onReloadPage, students]);

  window.onclick = (e) => {
    const optionItem = document.getElementById("optionItem");
    if (e.target.id === "option" || e.target.id === "optionIcon") {
      if (optionItem.style.display === "flex") {
        optionItem.style.display = "none";
      } else {
        optionItem.style.display = "flex";
      }
    } else {
      if (optionItem !== null) {
        optionItem.style.display = "none";
      }
    }
    if (e.target.id === "delete") {
      handleDeleteClass();
    }
  };

  const handleDeleteClass = async () => {
    try {
      const url = "/class/delete/class?id=" + classInfo.classId;
      await axios
        .delete(url)
        .then((res) => {
          handleSuccess(res.data);
          const path = location.pathname.replace("/class/view", "/dashboard");
          navigate(path);
        })
        .catch((err) => {
          handleError(err?.response?.data?.message);
        });
    } catch (error) {
      handleError(error?.response?.data?.message);
    }
  };

  return (
    <React.Fragment>
      {loading ? (
        <Loading />
      ) : (
        <InfoPanel barient={theme[marks.classInfo.theme].color}>
          <OptionPanel id="option" onclick={() => setOpenOption(!openOption)}>
            <HiOutlineDotsVertical id="optionIcon" />
          </OptionPanel>

          <OptionItems id="optionItem" style={{ display: "none" }}>
            <ul
              style={{
                padding: "0.5rem",
              }}
            >
              <Li
                onClick={() => {
                  setOpenOption(!openOption);
                  students.handleOpen(true);
                }}
              >
                Add Students
              </Li>

              <Li
                onClick={() => {
                  marks.handleOpenBox(true);
                }}
              >
                Add Marks
              </Li>
              <Li id="delete">Delete</Li>
              <Li>Edit</Li>
            </ul>
          </OptionItems>
          {marks.classInfo && (
            <>
              <Title>{marks.classInfo.courseName}</Title>
              <SubTitle>
                {getSemester(marks.classInfo.year)} Year{" "}
                {getSemester(marks.classInfo.semester)} Semester
              </SubTitle>
            </>
          )}
          <SubTitle style={{ marginTop: "auto", fontSize: "0.89rem" }}>
            {" "}
            Session: {marks.classInfo.session}
          </SubTitle>
          <SubTitle style={{ marginTop: "auto", fontSize: "0.85rem" }}>
            {marks.classInfo.study_center}
          </SubTitle>
          <SubTitle
            style={{
              position: "absolute",
              bottom: "10px",
              fontWeight: 400,
              fontSize: "0.8rem",
            }}
          >
            {marks.classInfo.teacherName}
          </SubTitle>
          {/**
           * Open Box for Import student Name and ID
           * Download Sample File
           */}
          <ImportStudentInfo
            // openBox={open}
            // setOpenBox={setOpen}
            classInfo={marks.classInfo}
          />
          <AddMarks />

          {flag && (
            <ViewInfo classInfo={marks.classInfo} openBox={flag} id={id} />
          )}
        </InfoPanel>
      )}
    </React.Fragment>
  );
};

const Loading = () => {
  return (
    <InfoPanel
      barient="rgba(250,250,250,1)"
      style={{ border: "1px solid rgba(3,3,3,0.1)" }}
    >
      <Div
        style={{
          width: "60%",
          height: "35px",
        }}
      />
      <Div
        style={{
          width: "40%",
        }}
      />
      <Div
        style={{
          width: "30%",
        }}
      />
      <Div
        style={{
          width: "50%",
        }}
      />
      <Div2 />
    </InfoPanel>
  );
};

export default TopInformationPanel;

const getSemester = (num) => {
  if (num === 1) {
    return "1st";
  } else if (num === 2) {
    return "2nd";
  } else if (num === 3) {
    return "3rd";
  } else if (num === 4) {
    return "4th";
  } else {
    return "";
  }
};

export const InfoPanel = styled.div`
  position: relative;
  padding: 01rem;
  width: 100%;
  border-radius: 5px;
  color: #fff;
  min-height: 12rem;
  width: 100%;
  margin-bottom: 0.5rem;
  background: ${(props) =>
    props.barient ? props.barient : "rgba(52, 31, 151,1.0)"};
  overflow: hidden;
`;

export const Title = styled.h1`
  font-size: 1.2rem;
`;

export const SubTitle = styled.h5`
  font-size: 1rem;
`;

export const OptionPanel = styled.div`
  position: absolute;
  right: 5px;
  top: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  width: 35px;
  height: 35px;
  font-size: 1.4rem;
  cursor: pointer;
  &:hover {
    background: rgba(240, 240, 240, 0.4);
  }
`;

export const OptionItems = styled.div`
  position: absolute;
  display: flex;
  flex-direction: column;
  right: 35px;
  top: 15px;
  background: #fff;
  border-radius: 5px;
  box-shadow: 1px 1px 5px 4px rgba(3, 3, 3, 0.2);
  transition: all cubic-bezier(0.075, 0.82, 0.165, 1);
`;

export const Li = styled.li`
  color: #333;
  padding: 0.3rem 0.5rem;
  list-style-type: none;
  font-weight: 500;
  cursor: pointer;

  &:nth-last-child(1) {
  }
`;

const Div = styled.div`
  background: rgba(210, 210, 210, 1);
  border-radius: 50px;
  height: 25px;
  margin-bottom: 7px;

  &:nth-child(1) {
    margin-bottom: 20px;
  }
  animation-name: breath-animation;
  animation-duration: 1s;
  animation-iteration-count: infinite;

  @keyframes breath-animation {
    0% {
      opacity: 0.9;
    }
    25% {
      opacity: 0.6;
    }
    50% {
      opacity: 0.4;
    }
    75% {
      opacity: 0.6;
    }
    100% {
      opacity: 0.9;
    }
  }
`;

const Div2 = styled.div`
  position: absolute;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  background: rgba(210, 210, 210, 1);
  top: 13px;
  right: 10px;
  animation-name: breath-animation;
  animation-duration: 1s;
  animation-iteration-count: infinite;

  @keyframes breath-animation {
    0% {
      opacity: 0.9;
    }
    25% {
      opacity: 0.6;
    }
    50% {
      opacity: 0.4;
    }
    75% {
      opacity: 0.6;
    }
    100% {
      opacity: 0.9;
    }
  }
`;
